import SimpleLightbox from "simplelightbox";
import "simplelightbox/dist/simple-lightbox.min.css";
import '../css/ball-pulse.css';

const catCard = document.querySelector("#cat-card");
const loader = document.querySelector("#loader");

const lightbox = new SimpleLightbox(".gallery-item", {
  captionsData: "data-caption",
  captionDelay: 250,
});

export function createGallery(images) {
  const markup = images.map(
    ({ webformatURL, largeImageURL, tags, likes, views, comments, downloads }) => `
      <a href="${largeImageURL}" class="gallery-item" data-caption="
        ðŸ‘ Likes: ${likes}
        ðŸ‘ Views: ${views}
        ðŸ’¬ Comments: ${comments}
        â¬‡ Downloads: ${downloads}
      ">
        <div class="card">
          <img src="${webformatURL}" alt="${tags}" />
        </div>
      </a>
    `
  ).join("");

  catCard.innerHTML = markup;
  lightbox.refresh();
}

export function clearGallery() {
  catCard.innerHTML = "";
}

export function showLoader() {
  loader.style.display = "flex"; // Ð°Ð±Ð¾ "block", Ð·Ð°Ð»ÐµÐ¶Ð¸Ñ‚ÑŒ Ð²Ñ–Ð´ ÑÑ‚Ð¸Ð»ÑŽ
}

export function hideLoader() {
  loader.style.display = "none";
}
